.profileContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 1rem 6rem 1rem 1rem;
}

.profilePic {
  margin: 1rem;
}

.workplace {
  font-size: x-small;
}

.comment {
  font-weight: 100;
  font-size: small;
}

@media screen and (min-width: 767px) {
  .comment {
    font-size: large;
  }

  .workplace {
    font-size: medium;
  }

  .profilePic {
    width: 15rem;
  }
}
