.contact {
  color: var(--bright-blue-color);
  transition: color ease-in-out 300ms;
}

.contact:hover {
  color: var(--bright-orange-color);
}

.container {
  display: flex;
  padding: 1rem 0 8rem 0;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100% !important;
}

.container div {
  width: 100% !important;
}

.overallContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  max-width: 100% !important;
  margin-top: 4rem;
  transition: color ease-in-out 300ms;
}

.overallContainer {
  color: var(--bright-orange-color);
}

.hr {
  width: 3rem;
  height: 5px;
  border: 0;
  margin: 1rem 0;
  background-color: var(--bright-blue-color) !important;
  border-radius: 20px;
  transition: background-color ease-in-out 300ms;
}

.heading:hover .hr {
  background-color: var(--bright-orange-color) !important;
}

.heading {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  color: #000;
  transition: color ease-in-out 300ms;
}

.heading:hover {
  color: var(--bright-orange-color);
}

@media screen and (min-width: 767px) {
  .questions h2 button {
    font-size: 1.5rem !important;
    font-weight: bold;
  }

  .questions div {
    font-weight: normal;
  }
}
